<template>
	<div class="box">
		<div id="dv1" ref="hot">
			<div class="dv2" style="position: absolute;width: 100%;padding-bottom: 50px; overflow: auto;">
				<div class="content" v-for="item,index in movieList" @click="toDetail(item)">
					<div class="left">
						<img class="pic" :src="item.img" />
					</div>
					<div class="right">
						<div class="right_content">
							<p>
								<span>{{item.nm}}</span>
							</p>
							<p>
								<span>观众评</span>
								<span>{{item.sc}}</span>
							</p>
							<p class="js">{{item.star}}</p>
							<p>{{item.showInfo}}</p>
						</div>
						<div class="right_buy">
							<button>购买</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import BScroll from 'better-scroll'
	export default {
		//下边这两各钩子函数  只有在缓存的组件中才会被激活
		activated(){
			console.log("李鹏")
		},
		
		deactivated(){
			console.log("不见了")
		},
		data() {
			return {
				//				list: [],
				page: 0,
				movieList: [],
				movieIds: [],
				total: 0
			}
		},
		methods: {
			//			getData() {
			//				this.$http.get("/my/ajax/movieOnInfoList?token=").then((res) => {
			//					//console.log(res)
			//					this.list = res.data.movieList.filter((item, index) => {
			//						return item.img = item.img.replace("w.h", "128.180")
			//					})
			//				})
			//			},
			//		
			//详情页
			toDetail(item) {
				this.$router.push({
					path: "/detail",
					query: {
						list: item,
					}
				})
			},
			//下拉加载请求数据
			async getList() {
				this.page++;
				let url = '/my/ajax/movieOnInfoList?token=';

				let params = {};
				if(this.page > 1) {
					//rows 10
					//  1-10   0-9
					//  11-20  10-19  20
					let start = (this.page - 1) * 10;
					let end = start + 10;
					let arr = this.movieIds.slice(start, end);
					// console.log(arr.join('%'));
					url = '/my/ajax/moreComingList?token=';
					params = {
						params: {
							movieIds: arr.join(',')
						}
					}
					// 345655%2C1217351%2C341605%2C345036%2C1189514%2C1249199%2C1132519%2C1243911%2C1228046%2C1248656
				}
				let data = await this.$http.get(url, params);
				// console.log(data);
				if(this.page === 1) {
					this.movieList = this.movieList.concat(data.data.movieList);
					this.movieIds = [].concat(data.data.movieIds);
				}
				this.movieList = this.movieList.concat(data.data.coming);
				// console.log(this.movieList)
				this.movieList = this.movieList.filter(item => {
					return item.img = item.img.replace("w.h", "128.180")
				})
			}
		},
		created() {
			//			this.getData()
			this.getList()
		},
		mounted() {
			this.$nextTick(function () {
			  this.scroll1 = new BScroll('#dv1', {
				click: true,
				scrollY: true,
				bounce: false
			});
			//console.log(this.scroll1)
			let that = this;
			//监听下拉结束的时候 scrollEnd
			this.scroll1.on('scrollEnd', function(params) {
				let dis = Math.abs(params.y);
				let viewHeight = that.$refs.hot.clientHeight;
				//console.log(viewHeight)
				let h = that.$refs.hot.children[0].clientHeight;
				if((dis + viewHeight) >= h) {
					that.getList()
				}
			})
			})
			

		},
		updated() {
			this.scroll1.refresh();
		}
	}
</script>

<style scoped>
	.box{
		padding-top:40px ;
	}
	.box #dv1 {
		width: 100%;
		height: calc(100vh - 95px);
	}
	
	.box {
		width: 100%;
		border-top: 1px solid #ccc;
	}
	
	.box .content {
		width: 100%;
		height: 114px;
		display: flex;
		justify-content: flex-start;
		padding: 12px 14px 12px 12px;
	}
	
	.box .content .left {
		width: 64px;
		height: 90px;
		margin-right: 10px;
	}
	
	.box .content .left>.pic {
		width: 100%;
		height: 100%;
	}
	
	.box .content .right {
		width: 269px;
		height: 88px;
		display: flex;
		justify-content: space-between;
	}
	
	.box .content .right .right_content {
		width: 202px;
		height: 88px;
	}
	
	.box .content .right .right_content .js {
		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;
	}
	
	.box .content .right .right_buy {
		width: 47px;
		height: 88px;
		line-height: 47px;
	}
</style>


